<template><section><div id="example"></div>

<h2 id="mdialog-dan-kuang-zu-jian"><a class="header-anchor" href="#mdialog-dan-kuang-zu-jian" aria-hidden="true">¶</a> MDialog 弹框组件</h2>
<p>创建一个简单的弹框的组件</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;body&gt;&lt;m-dialog :show.sync=\&quot;show1\&quot; title=\&quot;对话框\&quot;&gt;\n  &lt;p&gt;这是一段信息&lt;/p&gt;\n  &lt;div slot=\&quot;footer\&quot;&gt;\n    &lt;m-button @click=\&quot;show1 = false\&quot;&gt;关闭&lt;/m-button&gt;\n  &lt;/div&gt;\n&lt;/m-dialog&gt;\n&lt;m-button @click=\&quot;show1 = true\&quot;&gt;开启弹窗&lt;/m-button&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  export default {\n    data () {\n      return {\n        show1: false\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><body><m-dialog :show.sync="show1" title="对话框">
  <p>这是一段信息</p>
  <div slot="footer">
    <m-button @click="show1 = false">关闭</m-button>
  </div>
</m-dialog>
<m-button @click="show1 = true">开启弹窗</m-button>

</body></html></div>
                            <p>通过 <code>m-dialog</code> 组件，可以快速创建对话框，<code>show</code> 属性控制显示与关闭</p>

                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">m-dialog</span> <span class="hljs-attr">:show.sync</span>=<span class="hljs-string">"show1"</span> <span class="hljs-attr">title</span>=<span class="hljs-string">"对话框"</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">p</span>&gt;</span>这是一段信息<span class="hljs-tag">&lt;/<span class="hljs-name">p</span>&gt;</span>
  <span class="hljs-tag">&lt;<span class="hljs-name">div</span> <span class="hljs-attr">slot</span>=<span class="hljs-string">"footer"</span>&gt;</span>
    <span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"show1 = false"</span>&gt;</span>关闭<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
  <span class="hljs-tag">&lt;/<span class="hljs-name">div</span>&gt;</span>
<span class="hljs-tag">&lt;/<span class="hljs-name">m-dialog</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"show1 = true"</span>&gt;</span>开启弹窗<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    data () {
      <span class="hljs-keyword">return</span> {
        <span class="hljs-attr">show1</span>: <span class="hljs-literal">false</span>
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="alert-xiao-xi-kuang"><a class="header-anchor" href="#alert-xiao-xi-kuang" aria-hidden="true">¶</a> Alert 消息框</h2>
<p>提示消息</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;body&gt;&lt;m-button @click=\&quot;handleAlert1\&quot; type=\&quot;info\&quot;&gt;消息框&lt;/m-button&gt;\n&lt;m-button @click=\&quot;handleAlert2\&quot; type=\&quot;warning\&quot;&gt;警告消息&lt;/m-button&gt;\n&lt;m-button @click=\&quot;handleAlert3\&quot;&gt;并发多个消息&lt;/m-button&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  export default {\n    methods: {\n      handleAlert1 () {\n        this.$alert('这是一个条提示消息', {\n          typeIcon: 'info'\n        })\n      },\n      handleAlert2 () {\n        this.$alert('这是一个警告信息!', {\n          title: '警告',\n          typeIcon: 'warning',\n          confirmButtonType: 'warning',\n          confromButtonText: '确定'\n        })\n      },\n      handleAlert3 () {\n        for (let i = 0; i &lt; 5; i++) {\n          this.$alert(`这是第${i + 1}条提示消息`)\n        }\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><body><m-button @click="handleAlert1" type="info">消息框</m-button>
<m-button @click="handleAlert2" type="warning">警告消息</m-button>
<m-button @click="handleAlert3">并发多个消息</m-button>

</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleAlert1"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>消息框<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleAlert2"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"warning"</span>&gt;</span>警告消息<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleAlert3"</span> &gt;</span>并发多个消息<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      handleAlert1 () {
        <span class="hljs-keyword">this</span>.$alert(<span class="hljs-string">'这是一个条提示消息'</span>, {
          <span class="hljs-attr">typeIcon</span>: <span class="hljs-string">'info'</span>
        })
      },
      handleAlert2 () {
        <span class="hljs-keyword">this</span>.$alert(<span class="hljs-string">'这是一个警告信息!'</span>, {
          <span class="hljs-attr">title</span>: <span class="hljs-string">'警告'</span>,
          <span class="hljs-attr">typeIcon</span>: <span class="hljs-string">'warning'</span>,
          <span class="hljs-attr">confirmButtonType</span>: <span class="hljs-string">'warning'</span>,
          <span class="hljs-attr">confromButtonText</span>: <span class="hljs-string">'确定'</span>
        })
      },
      handleAlert3 () {
        <span class="hljs-keyword">for</span> (<span class="hljs-keyword">let</span> i = <span class="hljs-number">0</span>; i &lt; <span class="hljs-number">5</span>; i++) {
          <span class="hljs-keyword">this</span>.$alert(<span class="hljs-string">`这是第<span class="hljs-subst">${i + <span class="hljs-number">1</span>}</span>条提示消息`</span>)
        }
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
<h2 id="confirm-dui-hua-kuang"><a class="header-anchor" href="#confirm-dui-hua-kuang" aria-hidden="true">¶</a> Confirm 对话框</h2>
<p>对话框</p>
<demo-block class="demo-box" :jsfiddle="{&quot;html&quot;:&quot;&lt;html&gt;&lt;body&gt;&lt;m-button @click=\&quot;handleConfirm1\&quot; type=\&quot;info\&quot;&gt;对话框1&lt;/m-button&gt;\n&lt;m-button @click=\&quot;handleConfirm2\&quot; type=\&quot;danger\&quot;&gt;对话框2&lt;/m-button&gt;\n&lt;m-button @click=\&quot;handleConfirm3\&quot; type=\&quot;primary\&quot;&gt;对话框3&lt;/m-button&gt;\n\n&lt;/body&gt;&lt;/html&gt;&quot;,&quot;script&quot;:&quot;\n  export default {\n    methods: {\n      handleConfirm1 () {\n        this.$confirm('这是一条提示信息').then(() =&gt; {\n          alert('确认了操作')\n        }).catch(() =&gt; {\n          alert('取消了操作')\n        })\n      },\n      handleConfirm2 () {\n        this.$confirm('你确定删除吗？', {\n          typeIcon: 'danger',\n          confirmButtonType: 'danger'\n        }).then(() =&gt; {\n          // alert('确认了操作')\n        }).catch(() =&gt; {\n          // alert('取消了操作')\n        })\n      },\n      handleConfirm3 () {\n        this.$confirm('你确定删除ID为：&lt;strong&gt;1001&lt;/strong&gt; 的数据吗？', {\n          typeIcon: 'danger',\n          confirmButtonType: 'danger',\n          supperHTMLString: true\n        })\n      }\n    }\n  }\n&quot;,&quot;style&quot;:null}">
                            <div class="source" slot="source"><html><body><m-button @click="handleConfirm1" type="info">对话框1</m-button>
<m-button @click="handleConfirm2" type="danger">对话框2</m-button>
<m-button @click="handleConfirm3" type="primary">对话框3</m-button>

</body></html></div>
                            
                            <div class="highlight" slot="highlight"><pre v-pre=""><code v-pre="" class="hljs language-html"><span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleConfirm1"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"info"</span>&gt;</span>对话框1<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleConfirm2"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"danger"</span>&gt;</span>对话框2<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">m-button</span> <span class="hljs-attr">@click</span>=<span class="hljs-string">"handleConfirm3"</span> <span class="hljs-attr">type</span>=<span class="hljs-string">"primary"</span>&gt;</span>对话框3<span class="hljs-tag">&lt;/<span class="hljs-name">m-button</span>&gt;</span>
<span class="hljs-tag">&lt;<span class="hljs-name">script</span>&gt;</span><span class="javascript">
  <span class="hljs-keyword">export</span> <span class="hljs-keyword">default</span> {
    <span class="hljs-attr">methods</span>: {
      handleConfirm1 () {
        <span class="hljs-keyword">this</span>.$confirm(<span class="hljs-string">'这是一条提示信息'</span>).then(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          alert(<span class="hljs-string">'确认了操作'</span>)
        }).catch(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          alert(<span class="hljs-string">'取消了操作'</span>)
        })
      },
      handleConfirm2 () {
        <span class="hljs-keyword">this</span>.$confirm(<span class="hljs-string">'你确定删除吗？'</span>, {
          <span class="hljs-attr">typeIcon</span>: <span class="hljs-string">'danger'</span>,
          <span class="hljs-attr">confirmButtonType</span>: <span class="hljs-string">'danger'</span>
        }).then(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-comment">// alert('确认了操作')</span>
        }).catch(<span class="hljs-function"><span class="hljs-params">()</span> =&gt;</span> {
          <span class="hljs-comment">// alert('取消了操作')</span>
        })
      },
      handleConfirm3 () {
        <span class="hljs-keyword">this</span>.$confirm(<span class="hljs-string">'你确定删除ID为：&lt;strong&gt;1001&lt;/strong&gt; 的数据吗？'</span>, {
          <span class="hljs-attr">typeIcon</span>: <span class="hljs-string">'danger'</span>,
          <span class="hljs-attr">confirmButtonType</span>: <span class="hljs-string">'danger'</span>,
          <span class="hljs-attr">supperHTMLString</span>: <span class="hljs-literal">true</span>
        })
      }
    }
  }
</span><span class="hljs-tag">&lt;/<span class="hljs-name">script</span>&gt;</span>
</code></pre>
</div></demo-block>
</section></template>

<script>
  export default {
    data () {
      return {
        show1: false
      }
    },
    methods: {
      handleAlert1 () {
        this.$alert('这是一个条提示消息', {
          typeIcon: 'info'
        })
      },
      handleAlert2 () {
        this.$alert('这是一个警告信息!', {
          title: '警告',
          typeIcon: 'warning',
          confirmButtonType: 'warning',
          confromButtonText: '确定'
        })
      },
      handleAlert3 () {
        for (let i = 0; i < 5; i++) {
          this.$alert(`这是第${i + 1}条提示消息`)
        }
      },
      handleConfirm1 () {
        this.$confirm('这是一条提示信息').then(() => {
          alert('确认了操作')
        }).catch(() => {
          alert('取消了操作')
        })
      },
      handleConfirm2 () {
        this.$confirm('你确定删除吗？', {
          typeIcon: 'danger',
          confirmButtonType: 'danger'
        }).then(() => {
          // alert('确认了操作')
        }).catch(() => {
          // alert('取消了操作')
        })
      },
      handleConfirm3 () {
        this.$confirm('你确定删除ID为：<strong>1001</strong> 的数据吗？', {
          typeIcon: 'danger',
          confirmButtonType: 'danger',
          supperHTMLString: true
        })
      }
    }
  }
</script>


